import interfaces from '../utils/interface.js'
export default {
	props: ["showPay", "Price", "footerbottom", "order_num", "isGame"],
	data() {
		return {
			wechat: 0,
			zhifubao: 1,
			bankCard: 2,
			dealCur: true,
			showIndex: 0,
			/*支付部分*/
			title: 'request-payment',
			loading: false,
			price: 0.01, //支付价格
			alipayList: [], //支付宝支付
			wxpayList: [], //微信支付

		}
	},
	methods: {

		hidePay() {
			this.$emit('func', false)
			uni.showModal({
				title: '取消支付',
				content: '确定要取消支付吗？',
				cancelText: '否',
				confirmText: '是',
				success: res => {
					if (res.confirm) {
						uni.navigateTo({
							url: "../order"
						})
					} else {
						this.$emit('func', true)
					}
				}
			})
		},
		setIndex(n) { //切换支付方式
			this.showIndex = n;
		},
		showCur() { //选中已阅读
			console.log('13')
			this.dealCur = !this.dealCur;
		},
		/*小程序微信支付*/
		// weixinPay() {
		// 	console.log('微信支付')
		// 	// let url = 'https://www.tiktokshopee.com/apk/Play/aa';
		// 	// void plus.runtime.openWeb(url, function() { //跳转H5支付

		// 	// })

		// },























































































		aliPay() {
			
			console.log('支付宝支付')
			
			my.request({ //在小程序端调用 my.request 接口，连接到商户服务端
				method: 'POST',
				url: 'https://www.tiktokshopee.com/Alipay.php',
				data: {
					 // from: '支付宝',
					 // production: 'AlipayJSAPI',
				},
				headers:{
				    'content-type':'application/json'  //默认值
				},
				dataType: 'json',
				success: function(info) {
					console.log(info);
					my.tradePay({ //在小程序端使用 tradeNO，调用 my.tradePay 接口唤起支付收银台
						tradeNO: info,
						success: function(res) {
							my.alert(res)
						},
						fail: function(res) {
							my.alert(res)
						}
					})
				}
			})
		},

		/*APP支付*/
		Payment() {




































		},











		toRequestPayment() {
			var e, index;
			if (this.showIndex === 0) { //微信支付
				// e = this.wxpayList[0]; //App支付
				// index = 0;
				// this.requestPayment(e,index)
				let url = 'http://www.tiktokshopee.com/apk/Wxpay/H5jsapipay';
				void plus.runtime.openWeb(url, function() { //跳转H5支付

				})
			} else if (this.showIndex === 1) { //支付宝支付
				// e = this.alipayList[0]; //App支付
				// index = 1;
				// this.requestPayment(e ,index)
				let url = 'http://www.mamamohe.cn/zfb/alipay/index.php';
				void plus.runtime.openWeb(url, function() { //跳转H5支付

				})
			}
		},
		async requestPayment(e, index) {
			// console.log(JSON.stringify(e) , 666666666666)
			this.loading = true;
			let orderInfo = await this.getOrderInfo(e.id);
			console.log("得到订单信息", orderInfo);
			if (orderInfo.statusCode !== 200) {
				console.log("获得订单信息失败", orderInfo);
				uni.showModal({
					content: "获得订单信息失败",
					showCancel: false
				})
				return;
			}
			console.log(e.id, orderInfo.data)
			uni.requestPayment({
				provider: e.id,
				orderInfo: orderInfo.data,
				success: (e) => {
					console.log("success", e);
					uni.showToast({
						title: "感谢您的赞助!"
					})
				},
				fail: (e) => {
					console.log("fail", e);
					uni.showModal({
						content: "支付失败,原因为: " + e.errMsg,
						showCancel: false
					})
				},
				complete: () => {
					this.loading = false;
				}
			})
		},
		getOrderInfo(e) {
			let appid = "";



			//https://demo.dcloud.net.cn/payment/?payid=wxpay&appid=HBuilder&total=1
			let url = 'https://demo.dcloud.net.cn/payment/?payid=' + e + '&appid=' + appid + '&total=' + this.price;
			// let url = 'https://demo.dcloud.net.cn/payment/?payid=wxpay&appid=HBuilder&total=0.01';
			// let url =  'http://www.mamamohe.cn/wx/pay.php';
			return new Promise((res) => {
				uni.request({
					url: url,
					success: (result) => {
						res(result);
					},
					fail: (e) => {
						res(e);
					}
				})
			})
		}

	}

}
